  text-align: center;
  text-transform: uppercase;
  color: #060b35;
  align-items: center;
  height: 60px;
  font-size: 12px;
  line-height: 14px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__list .nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: " ";
  background-color: #00acdc;
  border-radius: 500px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.catalog {
  --columns: 1;
  grid-template-columns: repeat(var(--columns), 200px);
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    --columns: 2;
  }
}

@media (width >= 768px) {
  .catalog {
    --columns: 3;
  }
}

@media (width >= 1024px) {
  .catalog {
    --columns: 4;
  }
}

.container {
